import request from '../../request'


//     
export function listAffairCost(data) {
    return request({
        url: '/api/affairCost/listAffairCost',
        method: 'post',
        data
    })
}

// 
export function saveAffairCost(data) {
    return request({
        url: '/api/affairCost/saveAffairCost',
        method: 'post',
        data
    })
}

// 
export function updateAffairCost(data) {
    return request({
        url: '/api/affairCost/updateAffairCost',
        method: 'post',
        data
    })
}

// 
export function deleteAffairCostById(params) {
    return request({
        url: '/api/affairCost/deleteAffairCostById',
        method: 'get',
        params: params
    });
}




















