<template>
  <div class="moduleBoxView">
    <div class="moduleFrom">
      <el-form :inline="true" :model="fromData" class="demo-form-inline" :label-width="80" label-position="right">
        <el-row :gutter="0">
          <el-col :span="4"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('AfterSpu.afterOrderNumber')">
              <el-input v-model="fromData.afterOrderNumber" :placeholder="$t('AfterSpu.afterOrderNumberPlaceholder')" style="width: 100%"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="4"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('AfterSpu.problemTitle')">
              <el-select v-model="fromData.problemId" filterable :placeholder="$t('AfterSpu.problemTitlePlaceholder')" :disabled="isReadonly" style="width: 100%">
                <el-option v-for="item in selectData.problemList" :key="item.id" :label="item.title" :value="item.id"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="4"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('AfterSpu.spuName')">
              <el-input v-model="fromData.spuName" :placeholder="$t('AfterSpu.spuNamePlaceholder')"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="4"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('AfterSpu.function')">
              <el-select filterable v-model="fromData.function" :placeholder="$t('AfterSpu.functionPlaceholder')" >
                <el-option v-for="item in selectData.functionList" :key="item.id" :label="item.name" :value="item.id"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="4"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('AfterSpu.series')">
              <el-select filterable v-model="fromData.series" :placeholder="$t('AfterSpu.seriesPlaceholder')">
                <el-option v-for="item in selectData.seriesList" :key="item.id" :label="item.name" :value="item.id"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="4"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('AfterSpu.familyName')">
              <el-input v-model="fromData.familyName" :placeholder="$t('AfterSpu.familyNamePlaceholder')"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="0">
          <el-col :span="4"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('AfterSpu.email')">
              <el-input v-model="fromData.email" :placeholder="$t('AfterSpu.emailPlaceholder')" style="width: 100%"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="4"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('AfterSpu.spuSequence')">
              <el-input v-model="fromData.spuSequence" :placeholder="$t('AfterSpu.spuSequencePlaceholder')" style="width: 100%"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="4"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('AfterSpu.buyDitchName')">
              <el-select filterable v-model="fromData.buyDitch" :placeholder="$t('AfterSpu.buyDitchNamePlaceholder')" style="width: 100%">
                <el-option v-for="item in selectData.ditchList" :key="item.dictValue" :label="item.dictKey" :value="item.dictValue"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="4"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('AfterSpu.region')">
              <el-select v-model="fromData.region" filterable :placeholder="$t('AfterSpu.regionPlaceholder')" :disabled="isReadonly" style="width: 100%">
                <el-option v-for="item in selectData.regionList" :key="item.id" :label="item.name" :value="item.id"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="4"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('AfterSpu.disposeUser')">
              <el-select filterable v-model="fromData.disposeUser" :placeholder="$t('AfterSpu.disposeUserPlaceholder')">
                <el-option v-for="item in selectData.userList" :key="item.id" :label="item.name" :value="item.id"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="4"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('AfterSpu.afterStatus')">
              <!-- <el-select filterable v-model="fromData.afterStatus" placeholder="...">-->
              <!--   <el-option v-for="item in selectData.afterStatusList" :key="item.id" :label="item.name" :value="item.id"></el-option>-->
              <!-- </el-select>-->
              <el-select v-model="fromData.afterStatusList" multiple collapse-tags collapse-tags-tooltip :max-collapse-tags="1" :placeholder="$t('AfterSpu.afterStatusPlaceholder')" style="width: 100%">
                <el-option v-for="item in selectData.afterStatusList" :key="item.id" :label="item.name" :value="item.id"/>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item :label="$t('AfterSpu.createDate')">
          <el-date-picker v-model="fromData.createTimes"
                          format="YYYY-MM-DD" value-format="YYYY-MM-DD"
                          type="daterange" :start-placeholder="$t('pullDown.daterange.start')"
                          unlink-panels :range-separator="$t('pullDown.daterange.range')"
                          :end-placeholder="$t('pullDown.daterange.end')"
          ></el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="listData()" icon="Search"> {{ $t('common.button.search') }} </el-button>
          <el-button type="info" @click="reset()" icon="Loading">  {{ $t('common.button.reset') }} </el-button>
          <el-button type="success" v-show="isButton('afterSpu.add')" @click="add()"> {{ $t('common.button.add') }} </el-button>
        </el-form-item>
      </el-form>
    </div>

    <div class="moduletTable" ref="tableDiv">
      <el-table :data="tableData" :height="tabHeight" stripe border fit highlight-current-row :empty-text="$t('common.table.noData')" style="width: 100%; margin-bottom: 5px;">
        <el-table-column fixed type="index" align="center" :label="$t('common.table.number')" width="90"/>
        <el-table-column prop="afterOrderNumber" :label="$t('AfterSpu.afterOrderNumber')" fixed="left" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="problemTitle" :label="$t('AfterSpu.problemTitle')" width="130" show-overflow-tooltip></el-table-column>
        <el-table-column prop="spuName" :label="$t('AfterSpu.spuName')" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="functionName" :label="$t('AfterSpu.function')" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="seriesName" :label="$t('AfterSpu.series')" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="familyName" :label="$t('AfterSpu.familyName')" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="summary" :label="$t('AfterSpu.summary')" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="email" :label="$t('AfterSpu.email')" width="280" show-overflow-tooltip></el-table-column>
        <el-table-column prop="spuSequence" :label="$t('AfterSpu.spuSequence')" width="230" show-overflow-tooltip></el-table-column>
        <el-table-column prop="buyTime" :label="$t('AfterSpu.buyTime')" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="buyDitchName" :label="$t('AfterSpu.buyDitchName')" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="ditchRemark" :label="$t('AfterSpu.ditchRemarks')" width="280" show-overflow-tooltip></el-table-column>
        <el-table-column prop="regionName" :label="$t('AfterSpu.region')" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="prove" :label="$t('AfterSpu.prove')" width="300" show-overflow-tooltip></el-table-column>
        <el-table-column prop="vedioUrl" :label="$t('AfterSpu.videoUrl')" width="300" show-overflow-tooltip></el-table-column>
        <el-table-column prop="detail" :label="$t('AfterSpu.detail')" width="300" show-overflow-tooltip></el-table-column>


        <!--      <el-table-column prop="title" label="" width="180" show-overflow-tooltip></el-table-column>-->
        <el-table-column prop="remarks" :label="$t('AfterSpu.remarks')" width="250" show-overflow-tooltip></el-table-column>
        <el-table-column prop="createTime" :label="$t('AfterSpu.createTime')" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="revampTime" :label="$t('AfterSpu.revampTime')" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="finishTime" :label="$t('AfterSpu.finishTime')" width="180" show-overflow-tooltip></el-table-column>
        <!--      <el-table-column prop="color" label="" width="180" show-overflow-tooltip></el-table-column>-->
        <el-table-column prop="disposeUserNmae" :label="$t('AfterSpu.disposeUser')" width="100" fixed="right" align="center" show-overflow-tooltip></el-table-column>
        <el-table-column prop="afterStatus" :label="$t('AfterSpu.afterStatus')" width="100" fixed="right" align="center" show-overflow-tooltip>
          <template v-slot="{ row }">
            <el-tag v-show="row.afterStatus == 1">{{ $t('pullDown.afterStatus.allocation') }}</el-tag>
            <el-tag class="ml-2" v-show="row.afterStatus == 2" type="success">{{ $t('pullDown.afterStatus.pending') }}</el-tag>
            <el-tag class="ml-2" v-show="row.afterStatus == 3" type="warning">{{ $t('pullDown.afterStatus.processing') }}</el-tag>
            <el-tag class="ml-2" v-show="row.afterStatus == 4" type="info">{{ $t('pullDown.afterStatus.completed') }}</el-tag>
            <el-tag class="ml-2" v-show="row.afterStatus == 5" type="danger">{{ $t('pullDown.afterStatus.unknown') }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column fixed="right" :label="$t('common.table.operation')" width="480">
          <template #default="scope">
            <el-button type="primary" size="small" @click="goOperate(scope.$index, scope.row, 'detail')" plain> {{ $t('common.button.detail') }} </el-button>
            <el-button type="warning" size="small" @click="goOperate(scope.$index, scope.row, 'edit')" plain v-show="isButton('afterSpu.update') && scope.row.afterStatus != 4"> {{ $t('common.button.edit') }} </el-button>
            <el-button type="danger" size="small" @click="deleteRow(scope.$index, scope.row)" plain v-show="isButton('afterSpu.delete')  && scope.row.afterStatus != 4"> {{ $t('common.button.delete') }} </el-button>
            <el-button type="success" size="small" @click="gotoFlow(scope.$index, scope.row)" plain v-show="scope.row.disposeUser && isButton('afterSpu.flow')"> {{ $t('common.button.progress') }} </el-button>
            <el-button type="info" size="small" @click="gotoAffair(scope.$index, scope.row)" plain v-show="scope.row.disposeUser"> {{ $t('common.button.record') }} </el-button>

            <el-dropdown style="margin-left: 12px" v-if="scope.row.afterStatus != 4">
              <el-button type="primary" size="small" plain>
                {{ $t('common.button.allocation') }} <el-icon class="el-icon--right"><arrow-down /></el-icon>
              </el-button>
              <template #dropdown>
                <el-dropdown-menu>
                  <el-dropdown-item @click="allotAfter(1, scope.row)" v-if="!scope.row.disposeUser && userSelectAfter === 1"> {{ $t('common.button.appoint') }} </el-dropdown-item>
                  <el-dropdown-item @click="allotAfter(2, scope.row)" v-if="!scope.row.disposeUser" > {{ $t('common.button.receive') }} </el-dropdown-item>
                  <el-dropdown-item @click="allotAfter(3, scope.row)" v-if="scope.row.disposeUser && scope.row.careofId == 0"> {{ $t('common.button.transfer') }} </el-dropdown-item>
                  <el-dropdown-item @click="allotAfter(4, scope.row)" v-if="scope.row.disposeUser && scope.row.careofId != 0"> {{ $t('common.button.transfering') }} </el-dropdown-item>
                  <el-dropdown-item @click="allotAfter(5, scope.row)" v-if="scope.row.disposeUser && scope.row.careofId != 0"> {{ $t('common.button.cancelTransfer') }} </el-dropdown-item>
                </el-dropdown-menu>
              </template>
            </el-dropdown>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div class="modulePagination">
      <el-pagination
          background
          v-model:current-page="pageNum"
          v-model:page-size="pageSize"
          :page-sizes="[10, 30, 50, 75, 100]"
          :small="false"
          layout="prev, pager, next, sizes, jumper, total"
          :total="total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
      />
    </div>
    <afterSpu-operate ref="operate" @operateClick="listData"/>
    <afterSpu-flow ref="flow" @operateClick="listData"/>
    <afterSpu-affair ref="affair" @operateClick="listData"/>
    <afterSpuUser ref="setUser" @operateClick="listData"/>
    <afterCareof-operate ref="careof"/>
  </div>
</template>

<script>
import {resourceAndAfterIsUser, resourceIsUser} from "@/util/resource";
import {ElLoading, ElNotification} from "element-plus";
import afterSpuOperate from "@/views/spuOred/afterSpu/afterSpu-operate";
import {getFunctionPullDown, getSeriesPullDown, getUserAdminPullDown} from "@/http/PullDownApi";
import {deleteAfterById, listAfter, updateAfter} from "@/http/spuOred/afterSpu/afterApi";
import {getDictPullDownClientele, getRegionPullDown, listProblemClientele} from "@/http/clienteleApi";
import afterSpuFlow from "@/views/spuOred/afterSpu/afterSpu-flow";
import afterSpuAffair from "@/views/spuOred/afterSpu/afterSpu-affair";
import {getNewYmdStr} from "@/util/DateUtil";
import afterSpuUser from "@/views/spuOred/afterSpu/afterSpu-user";
import afterCareofOperate from "@/views/spuOred/afterCareof/afterCareof-operate";
import {updateAfterCareof} from "@/http/spuOred/careofApi";

export default {
  name: "styleVeiw",
  components: {afterSpuOperate, afterSpuFlow, afterSpuAffair, afterSpuUser, afterCareofOperate},
  // data: 
  data: function () {
    return {
      tabHeight: 0, // 
      //  1:  | 2:  | 3: 
      userSelectAfter: resourceAndAfterIsUser(),
      // 
      selectData: {
        afterStatusList: [
          {id: 1, name: this.$t('pullDown.afterStatus.allocation')},
          {id: 2, name: this.$t('pullDown.afterStatus.pending')},
          {id: 3, name: this.$t('pullDown.afterStatus.processing')},
          {id: 4, name: this.$t('pullDown.afterStatus.completed')},
        ]
      },
      homeType: '', // 
      fromData: {
        afterStatusList: [1, 2, 3, 4], // 
        createTimes: [], // 
      }, // 

      tableData: [],
      total: 0,           // 
      pages: 0,           // 
      pageNum: 1,         //
      pageSize: 30,        // 
    }
  },

  // moubted: 
  mounted() {
    this.refreshTableHeight();
    this.getSelectData();
    //  
    this.judgeHomeType();
  },

  // methods: 
  methods: {
    // 
    refreshTableHeight() {
      //  ResizeObserver 
      const resizeObserver = new ResizeObserver(entries => {
        // entries  ResizeObserverEntry 
        for (const entry of entries) {
          if (entry.target === this.$refs.tableDiv) {
            // 
            // console.log('', entry.contentRect.height);
            this.tabHeight = this.$refs.tableDiv.offsetHeight - 20;
          }
        }
      });
      //  ResizeObserver 
      resizeObserver.observe(this.$refs.tableDiv);
    },
    // 
    isButton(symbolStr) {
      return resourceIsUser(symbolStr);
    },
    getUrl(photoUrl) {
      return process.env.VUE_APP_BACKEND_API_URL + photoUrl;
    },

    judgeHomeType() {
      if (this.$route.query.afterStatusList) {
        // console.log('', this.$route.query);
        this.fromData.disposeUser = this.$route.query.disposeUser == null ? null : Number(this.$route.query.disposeUser);
        this.fromData.afterStatusList = [];
        this.$route.query.afterStatusList.forEach(item => {
          this.fromData.afterStatusList.push(Number(item));
        });
        if (this.$route.query.createTimes && this.$route.query.createTimes.length == 2) {
          this.fromData.createTimes = this.$route.query.createTimes;
        }
        this.listData();
      } else {
        // console.log('')
        this.listData();
      }
    },

    // 
    getSelectData() {
      getSeriesPullDown().then(res => {
        if (res.data.status == 200) this.selectData.seriesList = res.data.data;
      }).catch((erro) => { });
      getFunctionPullDown().then(res => {
        if (res.data.status == 200) this.selectData.functionList = res.data.data;
      }).catch((erro) => { });
      getUserAdminPullDown().then(res => {
        if (res.data.status == 200) this.selectData.userList = res.data.data;
      }).catch((erro) => { });

      // 
      listProblemClientele().then(res => {
        if (res.data.status == 200) this.selectData.problemList = res.data.data;
      }).catch((erro) => { });
      //  
      getRegionPullDown().then(res => {
        if (res.data.status == 200) this.selectData.regionList = res.data.data;
      }).catch((erro) => { });
      // 
      getDictPullDownClientele({dictCode: 'GMQD'}).then(res => {
        if (res.data.status == 200) this.selectData.ditchList = res.data.data;
      }).catch((erro) => { });
    },
    // 
    listData() {
      const loading = ElLoading.service({
        lock: true, text: this.$t('common.loadingText'), background: 'rgba(0, 0, 0, 0.7)',}
      );
      if (this.fromData.afterStatusList.length == 0) {
        this.fromData.afterStatusList = [1, 2, 3, 4];
      }
      if (this.fromData.createTimes && this.fromData.createTimes.length == 2) {
        this.fromData.startDate = this.fromData.createTimes[0];
        this.fromData.endData = this.fromData.createTimes[1];
      }
      this.fromData.pageSize = this.pageSize;
      this.fromData.pageNum = this.pageNum;
      listAfter(this.fromData).then(res => {
        this.tableData = res.data.data.list;
        // 
        this.total = res.data.data.total;
        this.pages = res.data.data.pages;
        this.pageNum = res.data.data.pageNum;
        this.pageSize = res.data.data.pageSize;
        loading.close();
      }).catch((erro) => {
        loading.close();
      });
    },
    //  val=
    handleSizeChange(val) {
      console.log(` ${val} `);
      this.pageNum = 1;                       // 1
      this.pageSize = val;               // 
      this.listData();
    },
    //   
    handleCurrentChange(val) {
      console.log(`: ${val}`);
      this.pageNum = val;                 // 
      this.listData();
    },
    // 
    reset() {
      this.fromData = {
        afterStatusList: [1, 2, 3, 4], // 
        createTimes: [],
      };
      this.listData();
    },

    // 
    add() {
      this.$refs.operate.dialogFormVisible = true;
      this.$refs.operate.type = "add";
      this.$refs.operate.form.buyTime = getNewYmdStr(new Date());
      this.$refs.operate.getPullDownData();
    },
    //  /  
    goOperate(index, row, type) {
      this.$refs.operate.dialogFormVisible = true;
      this.$refs.operate.type = type;
      if (type == "detail") {
        //  | 
        this.$refs.operate.isReadonly = true;
        // this.$refs.operate.form = JSON.parse(JSON.stringify(row));
      } else if (type == "edit") {
        this.$refs.operate.rowData = row;
        // this.$refs.operate.form = JSON.parse(JSON.stringify(row));
      }
      this.$refs.operate.getPullDownData();
      this.$refs.operate.getRow(row.id);
    },
    // 
    deleteRow(index, row) {
      this.$confirm(this.$t('common.dialog.deleteTips') , this.$t('common.dialog.deleteTitle'), {
        confirmButtonText: this.$t('common.button.confirm'), cancelButtonText: this.$t('common.button.cancel'), type: 'warning'
      }).then(() => {
        // 
        deleteAfterById({ id: row.id}).then(res => {
          if (res.data.status == 200) {
            // 
            this.tableData.splice(index, 1);
            ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
          }
        }).catch(error => { });
      })
    },
    // 
    gotoFlow(index, row) {
      this.$refs.flow.drawer = true;
      this.$refs.flow.form = row;
      this.$refs.flow.getRow(row.selcetUrl);
    },
    // 
    gotoAffair(index, row) {
      this.$refs.affair.dialogFormVisible = true;
      this.$refs.affair.row = row;
      this.$refs.affair.gotoExecute();
    },

    //  1: 2: 3:
    allotAfter(type, row) {
      if (type == 1) {
        this.$refs.setUser.rowData = row;
        this.$refs.setUser.dialogFormVisible = true;
        this.$refs.setUser.type = 'setUser';
        this.$refs.setUser.getPullDownData();
      } else if (type == 2) {
        this.$confirm(this.$t('AfterSpu.receiveTxt'), this.$t('common.dialog.title'), {confirmButtonText: this.$t('common.button.confirm'), cancelButtonText: this.$t('common.button.cancel'), type: 'warning'}).then(() => {
          let user = JSON.parse(window.localStorage.getItem("account"));
          row.disposeUser = user.id;
          updateAfter(row).then(res => {
            if (res.data.status == 200) {
              ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
              // 
              this.listData();
            }
          }).catch((erro) => {});
        })
      } else if (type == 3) {
        this.$refs.setUser.rowData = row;
        this.$refs.setUser.dialogFormVisible = true;
        this.$refs.setUser.type = 'alterUser';
        this.$refs.setUser.getPullDownData();
      } else if (type == 4) {
        this.$refs.careof.dialogFormVisible = true;
        this.$refs.careof.type = 'detail';
        this.$refs.careof.rowData.id = row.careofId;
        this.$refs.careof.gotoExecute();
      } else if (type == 5) {
        // 
        this.$confirm(this.$t('AfterSpu.transferTxt'), this.$t('common.dialog.title'), {confirmButtonText: this.$t('common.button.confirm'), cancelButtonText: this.$t('common.button.cancel'), type: 'warning'}).then(() => {
          let data = {
            id: row.careofId,
            afterOrderNumber: row.afterOrderNumber,
            careofStatus: 4,
          }
          updateAfterCareof(data).then(res => {
            if (res.data.status == 200) {
              ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
              // 
              this.listData();
            }
          }).catch((erro) => {});
        })
      }
    },
  },
}
</script>

<!--css scoped:-->
<style scoped>
